@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header__list {
   display: flex;
   align-items: center;
}

.header__list li {
   margin-left: 60px;
}

.header__list a {
   padding: 5px 25px;
   border-radius: 15px;
   color: #fff;
   text-decoration: none;
   font-size: 14px;
}

.header__list a:hover {
   border: 2px solid #009AD8;
   padding: 5px 23px;
   color: #009AD8;
}
.header__burger {
   display: none;
   margin-top: 10px;
}

.burger__deactive {
   width: 40px;
   height: 40px;
   background: url(data:image/png;base64,) center / contain no-repeat;
}

.burger__active {
   width: 40px;
   height: 40px;
   background: url(data:image/png;base64,) center / contain no-repeat;
}
.menu{
   background-color: #1E202B;
   transition: all 0.6s;
   display: none;
}

.menu.mdeact {
   transform: translate(0px, -300px);
}

.menu.act {
   transform: translate(0px, 0px);
}

.menu__link {
   text-align: center;
   padding: 20px 0px;
}

.menu__link a {
   font-size: 18px;
   color: #fff;
   text-decoration: none;
}
.container {
   max-width: 1180px;
   margin: 0 auto;
   padding: 0 15px;
   height: 100%;
}

.header {
   position: relative;
   z-index: 3;
   height: 130px;
   background-color: #1E202B;
   color: #fff;
}

.header__content {
   display: flex;
   justify-content: space-between;
   height: 100%;
   align-items: center;
}



.search__form {
   height: 60px;
   background-color: #1E202B;
   border-radius: 25px;
   display: flex;
   align-items: center;
   overflow: hidden;
}

.search__input {
   height: 100%;
   width: 100%;
   background-color: transparent;
   padding: 0px 25px;
   margin: 0;
   border: 0;
   border: none;
   color: rgb(158, 158, 158);
   font-size: 18px;
   font-weight: 300;
}

.search__button {
   background-color: #009AD8;
   color: #fff;
   font-size: 18px;
   padding: 12px 40px;
   border-radius: 25px;
   margin: 0px 3px 0px 10px;
   border: none;
   cursor: pointer;
}

.search__button:active {
   transform: translate(0px, 2px);
}



.main__item-date {
   color: #BFC1C8;
   background-color: rgb(45, 48, 61);
   display: flex;
   align-items: center;
   justify-content: space-between;
   font-size: 14px;
   padding: 5px 15px;
}

.card__body {
   padding: 30px 20px;
   background-color: rgb(50, 53, 68);
   height: 250px;
}

.card__city {
   color: #BFC1C8;
   font-size: 20px;
}

.card__weather {
   display: flex;
   align-items: center;
}

.card__temp {
   font-size: 90px;
   font-weight: 700;
   color: #fff;
   margin-right: 40px;
}

.card__img img {
   width: 100px;
   height: 100px;
}

.details {
   display: flex;
   align-items: center;
   justify-content: space-between;
   color: #BFC1C8;
   font-weight: 300;
}

.det {
   display: flex;
}

.humidity__img {
   width: 21px;
   height: 21px;
   background: url(data:image/png;base64,) center / contain no-repeat;
   margin-right: 10px;
}

.wind__img {
   margin-right: 10px;
   width: 21px;
   height: 21px;
   background: url(data:image/png;base64,) center / contain no-repeat;
}

.preassure__img {
   margin-right: 10px;
   width: 21px;
   height: 21px;
   background: url(data:image/png;base64,) center / contain no-repeat;
}
.main {
   
}

.bg__city {
   height: 350px;
   /* background: url('../../img/newbg.jpg') 0 0 / cover no-repeat; */
   background-color: grey;
}

.bg__darkblue {
   min-height: 900px;
   background-color: #1E202B;
}

.search {
   position: relative;
   margin-top: -1170px;
   margin-bottom: 100px;
}

.weather {

}

.weather__days {
   display: grid;
   grid-template-columns: 3fr repeat(6, 1fr);
   grid-template-rows: 280px;
   border-radius: 15px;
   overflow: hidden;
}


.weather__item {
}

.weather__item-day {
   text-align: center;
   font-size: 14px;
   padding: 5px;
   color: #BFC1C8;
   background-color: rgb(45, 48, 61);
}

.bg>.weather__item-day {
   background-color: rgb(34, 37, 48);
}

.weather__item-body {
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   height: 250px;
   background-color: rgb(50, 53, 68);
}

.weather__item-img {
   width: 60px;
   height: 60px;
   margin-bottom: 20px;
   
}

.weather__item-temp {
   font-size: 24px;
   font-weight: 700;
   text-align: center;
   color: #fff;
   margin-bottom: 20px;
}

.weather__item-humid {
   font-weight: 300;
   color: #BFC1C8;
   font-size: 14px;
   opacity: 0.7;
}

.bg>.weather__item-body {
   background-color: rgb(38, 41, 54);
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Убираем внутренние отступы */
ul[class],
ol[class] {
  padding: 0;
}

/* Убираем внешние отступы */
body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

/* Выставляем основные настройки по-умолчанию для body */
body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

h1,
h2,
h3,
h4 {
   font-size: 16px;
}

/* Удаляем стандартную стилизацию для всех ul и il, у которых есть атрибут class*/
ul[class],
ol[class] {
  list-style: none;
}

/* Элементы a, у которых нет класса, сбрасываем до дефолтных стилей */
a:not([class]) {
  -webkit-text-decoration-skip: ink;
          text-decoration-skip-ink: auto;
}

/* Упрощаем работу с изображениями */
img {
  max-width: 100%;
  display: block;
}

/* Указываем понятную периодичность в потоке данных у article*/
article > * + * {
  margin-top: 1em;
}

/* Наследуем шрифты для инпутов и кнопок */
input,
button,
textarea,
select {
  font: inherit;
}

/* Удаляем все анимации и переходы для людей, которые предпочитай их не использовать */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
body {
   font-family: 'Roboto';
}



@media(max-width: 1000px) {
   .weather__days {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      grid-template-rows: 250px 280px;
      grid-row-gap: 25px;
      row-gap: 25px;
   }

   .weather__main-item {
      grid-column: 1 / 8;
   }
}

@media(max-width: 830px) {
   .header {
      height: 100px;
   }

   .header__list li {
      margin-left: 35px;
   }
}

@media(max-width: 720px) {
   .header {
      height: 80px;
   }

   .header__list {
      display: none;
   }

   .header__burger {
      display: block;
   }

   .menu {
      display: block;
   }

   .menu.mdeact {
      display: none;
   }

   .search {
      margin-bottom: 70px;
      margin-top: -1190px;
   }
}

@media(max-width: 550px) {
   .weather__days {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: 250px 250px 280px;
      grid-row-gap: 25px;
      row-gap: 25px;
   }

   .weather__main-item {
      grid-column: 1 / 4;
   }
}

@media(max-width: 420px) {
   .search__button {
      padding: 12px 20px;
   }
   .card__temp {
      font-size: 60px;
   }
   .card__weather {
      margin-bottom: 30px;
      justify-content: space-between;
   }
}

@media(max-width: 370px) {
   .search__form {
      display: flex;
      justify-content: space-between;
   }
   .search__input {
      width: 100%;
   }
}

.header__logo {
   display: flex;
   align-items: center;
}

.header__logo-img {
   transform: rotate3d(1, 1, 1, 0deg);
   width: 64px;
   height: 64px;
   margin-right: 15px;
   background: url(data:image/png;base64,)center / contain no-repeat;
   animation-name: logo;
   animation-duration: 4s;
   animation-timing-function: linear;
   animation-iteration-count: infinite;
}

@keyframes logo{
   0%{
      transform: rotate3d(0, 0, 0, 0deg);
   }

   100%{
      transform: rotate3d(0, 1, 0, 360deg);
   }
}

.header__logo-name h1{
   font-size: 18px;
   font-weight: 700;
   margin-bottom: 5px;
   white-space: nowrap;
}

.header__logo-subname h2{
   font-size: 12px;
   font-weight: 300;
   white-space: nowrap;
}
